import React from 'react'
import { Form, Input, Button, Checkbox } from 'antd'
import Title from 'antd/lib/typography/Title'
import { Link } from 'react-router-dom'

import './LoginPage.scss'
import CustomLink from '../../components/CustomLink'

const LoginForm = (props) => {
  const onFinish = (values) => {
    alert(', ,     .')
    console.log('Received values of form: ', values)
  }

  return (
    <div className='formWrapper theme'>
      <Form
        name='login-form'
        className='loginForm theme'
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
      >
        <Title className='loginForm__title' level={2}>
          
        </Title>
        <Form.Item
          name='username'
          rules={[
            {
              required: true,
              message: '    !',
            },
          ]}
        >
          <label className='theme'>
             
            <Input placeholder=' ' />
          </label>
        </Form.Item>

        <Form.Item
          name='password'
          rules={[
            {
              required: true,
              message: '  !',
            },
          ]}
        >
          <label className='theme'>
            
            <Input.Password type='password' placeholder=' ' />
          </label>
        </Form.Item>

        <Form.Item>
          <Form.Item name='remember' noStyle valuePropName='checked'>
            <Checkbox className='theme'> </Checkbox>
          </Form.Item>

          <Link className='loginForm__forgot' to='/recovery_password'>
             ?
          </Link>
        </Form.Item>

        <Form.Item>
          <Button htmlType='submit' className='loginForm__button'>
            
          </Button>
          <div className='loginForm--switcher theme'>
            {' '}
            <Link className='loginForm__linkToRegistration' to='/registration'>
               
            </Link>
          </div>
        </Form.Item>
      </Form>
      <CustomLink
        className='linkToSettings loginForm__button'
        to='/settings'
        tag={Button}
      >
          
      </CustomLink>
    </div>
  )
}

export default LoginForm
