import { Form, Input, Button, Checkbox } from "antd";
import Title from "antd/lib/typography/Title";
import { Link } from "react-router-dom";
import CustomLink from "../../components/CustomLink";

import "./RegistrationPage.scss";

const RegistrationPage = (props) => {
  const onFinish = (values) => {
    alert(
      ", email, ,  ,     ."
    );
    console.log("Received values of form: ", values);
  };

  return (
    <div className="formWrapper theme">
      <Form
        name="registration-form"
        className="regForm theme"
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
      >
        <Title className="regForm__title" level={2}>
          
        </Title>
        <Form.Item
          name="username"
          rules={[
            {
              required: true,
              message: "    !",
            },
          ]}
        >
          <label className="theme">
             
            <Input placeholder=" " />
          </label>
        </Form.Item>

        <Form.Item
          name="email"
          rules={[
            {
              type: "email",
              required: true,
              message: "    ",
            },
            {
              required: true,
              message: "   ",
            },
          ]}
        >
          <label className="theme">
            E-mail
            <Input placeholder=" " />
          </label>
        </Form.Item>

        <Form.Item
          name="password"
          rules={[
            {
              required: true,
              message: "  !",
            },
          ]}
          hasFeedback
        >
          <label className="theme">
            
            <Input.Password placeholder=" " />
          </label>
        </Form.Item>

        <Form.Item
          name="confirm"
          dependencies={["password"]}
          hasFeedback
          rules={[
            {
              required: true,
              message: "   !",
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("password") === value) {
                  return Promise.resolve();
                }

                return Promise.reject(
                  new Error("  !")
                );
              },
            }),
          ]}
        >
          <label className="theme">
             
            <Input.Password placeholder="  " />
          </label>
        </Form.Item>

        <Form.Item
          name="agreement"
          valuePropName="checked"
          rules={[
            {
              validator: (_, value) =>
                value
                  ? Promise.resolve()
                  : Promise.reject(
                      new Error("   ")
                    ),
            },
          ]}
        >
          <Checkbox className="theme">
             (){" "}
            <Link className="regForm__agreement" to="/agreement">
              
            </Link>
          </Checkbox>
        </Form.Item>

        <Form.Item>
          <Button htmlType="submit" className="regForm__button">
            
          </Button>
        </Form.Item>

        <div className="regForm--switcher theme">
          {" "}
          <Link className="regForm__linkToLogin" to="/login">
              
          </Link>
        </div>
      </Form>
      <CustomLink
        className='linkToSettings loginForm__button'
        to='/settings'
        tag={Button}
      >
          
      </CustomLink>
    </div>
  );
};

export default RegistrationPage;
