import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { Button } from 'antd';

import './Error.scss';

const Error = ({ text }) => {
    const tryToRepair = useCallback(() => {
        localStorage.removeItem('settings');
        window.location.replace(window.location.href);
    }, []);

    return (
        <div className="error--wrapper theme">
            <div className="error__text"><span>{text}</span></div>
            <div className="error__buttons">
                <Button className="button" onClick={() => {window.location.replace(window.location.href);}}></Button>
                <Button className="button" onClick={tryToRepair}> </Button>
            </div>
        </div>
    )
};

Error.propTypes = {
    error: PropTypes.string
};

export default Error;